"use client";

import { useState } from "react";

export default function RegisterPage() {
	const [age, setAge] = useState("");
	const [gender, setGender] = useState("");
	const [password, setPassword] = useState("");
	const [userId, setUserId] = useState(null);

	const generateUserId = () => {
		return "XC" + Math.floor(100000 + Math.random() * 900000);
	};

	const handleRegister = () => {
		if (!gender || !password) {
			alert("Gender and password are required!");
			return;
		}

		const newUserId = generateUserId();
		setUserId(newUserId);

		localStorage.setItem(
			"user",
			JSON.stringify({ age, gender, password, userId: newUserId })
		);

		alert(`Registration successful! Your User ID is ${newUserId}`);
	};

	return (
		<div className="flex flex-col items-center justify-center h-screen bg-gray-100">
			<h2 className="text-2xl font-bold mb-4">Register</h2>
			{userId ? (
				<p className="text-green-500 font-semibold">
					Your User ID: {userId} (Copy and save it!)
				</p>
			) : (
				<>
					<input
						type="number"
						placeholder="Age (Optional)"
						value={age}
						onChange={(e) => setAge(e.target.value)}
						className="border p-2 mb-2 w-64"
					/>
					<select
						value={gender}
						onChange={(e) => setGender(e.target.value)}
						className="border p-2 mb-2 w-64"
					>
						<option value="">Select Gender (Required)</option>
						<option value="Male">Male</option>
						<option value="Female">Female</option>
						<option value="Other">Other</option>
					</select>
					<input
						type="password"
						placeholder="Password (Required)"
						value={password}
						onChange={(e) => setPassword(e.target.value)}
						className="border p-2 mb-2 w-64"
					/>
					<button
						onClick={handleRegister}
						className="bg-blue-500 text-white p-2 w-64"
					>
						Register
					</button>
				</>
			)}
		</div>
	);
}
